export default {
	data() {
		return {
			note: '获得验证码',
			time: 30,
			timer: '',
			start: false,
			phone: '',
			code: '',
			code_ok: false,
			login_ok: false,
			old_phone: '',
			token: '',
		}
	},
	onLoad: function() {
		this.token = uni.getStorageSync('token')
		this.old_phone = uni.getStorageSync('phone')
	},
	onShow: function() {},
	methods: {
		//获取验证码
		getCode: function() {
			if (this.phone == '') {
				this.$myToast('手机号不能为空')

			} else {
				if ((/^1[3456789]\d{9}$/.test(this.phone))) {
					if (!this.code_ok) {
						this.timer = setInterval(() => {
							this.time--;
							if (this.time != 0) {
								this.note = this.time + '秒后重试'
								this.code_ok = true
							} else {
								this.note = '获取验证码'
								this.time = 30
								clearInterval(this.timer)
								this.code_ok = false
							}
						}, 1000)
						this.$myToast('发送成功')
						this.$urlRequest('getcode.do', {
							phone: this.phone
						}).then(res => {

						})

					}
				} else {
					this.$myToast('手机号格式不正确')
				}
			}
		},
		login: function() {
			if (this.phone == '') {
				this.$myToast('手机号不能为空')
				return;
			}
			if (!(/^1[3456789]\d{9}$/.test(this.phone))) {
				this.$myToast('手机号格式不正确')
				return
			}
			if (this.code == '') {
				this.$myToast('验证码不能为空')
				return
			}
			var data = {
				phone: this.phone,
				smscode: this.code,
				token: this.token
			}
			this.$urlRequest('updateUserphone.do', data).then(res => {
				if (res.state == "false") {
					this.$myToast(res.msg);
				} else if (res.state == "true") {
					this.$myToast('修改成功');
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						})
					}, 1500)
				}
			})

		}
	}
}
